div.tooltipster-base {
  font-family: "Roboto", sans-serif !important;
  text-transform: uppercase;
  ul {
    list-style-type: none;
    padding: 0;
  }
  div.tooltipster-arrow {
    display: none;
  }
  &.tooltipster-borderless {
    div.tooltipster-box {
      border-radius: 20px;
      border-color: #2d2d2d;
      background-color: #2d2d2d;
    }
  }
  &.tooltipster-shadow {
    text-transform: none;
    div.tooltipster-content {
      color: #000000;
    }
  }
  .mayusculas {
    text-transform: uppercase;
  }
  span.valor {
    display: inline-block;
    width: 2em;
    margin-right: 0.5em;
    font-weight: bold;
    text-align: right;
  }
}

body div.visualizacion {
  touch-action: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  *,
  *::before,
  *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
  }
  div.principal.espera {
    &::after {
      content: "";
      background: url(../img/loader.svg);
      background-color: lightgray;
      background-repeat: no-repeat no-repeat;
      background-position: center center;
      opacity: 0.8;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      position: fixed;
      z-index: 1;
    }
  }
  svg {
    touch-action: none;
    display: block;
    overflow: hidden;
  }

  circle {
    fill: unset;
  }

  .ignorar {
    pointer-events: none;
  }
  .cursor {
    cursor: pointer;
  }
  .arrastrar {
    cursor: grab;
  }
  .arrastrando {
    cursor: grabbing;
  }
  .invisible {
    opacity: 0;
    display: none;
  }
  image {
    &.arrastrar {
      touch-action: none;
    }
  }
}
